import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import Swal from "sweetalert2";
import { registerNotification } from "../../state/actions/webActions";
const Footer = () => {
  const [email, setEmail] = useState("");
  const [validEmail, setValidEmail] = useState(true);
  const dispatch = useDispatch();
  const onChange = (e) => {
    const { value } = e.target;
    setEmail(value);
    setValidEmail(true);
  };
  const onRegisterNotification = () => {
    const rex = /^[\w]{1,}[\w.+-]{0,}@[\w-]{2,}([.][a-zA-Z]{2,3}|[.][\w-]{2,3}[.][a-zA-Z]{2,3}|[.][\w-]{2,3}[.][a-zA-Z]{2,3}[.][a-zA-Z]{2,3})$/;
    if (rex.test(email)) {
      setValidEmail(true);
      dispatch(
        registerNotification(
          {
            payload: {
              email,
            },
          },
          (status) => {
            if (status) {
              setEmail("");
              Swal.fire({
                icon: "success",
                title: "Đăng kí thành công",
              });
            } else {
              Swal.fire({
                icon: "error",
                title: "Thất bại",
              });
            }
          }
        )
      );
    } else {
      setValidEmail(false);
    }
  };
  return (
    <footer id="footer" className="footer-1">
      <div className="footer-form">
        <img src="assets/image/footer.jpg" className="bg-form-footer" alt=""></img>
        <div className="blur-img">dfds</div>
        <div className="form">
          <h5>Đăng kí để nhận cập nhật</h5>
          <div className="mt-1">
            <img src="assets/image/icon-footer.png" height="20" alt="logo"></img>
          </div>

          <div>Để lại email để không bỏ lỡ bất kì ưu đãi nào</div>
          <div className="input-group mb-3">
            <input type="text" className="form-control" placeholder="Nhập email của bạn" name="email" onChange={onChange} />
            <button className="btn btn-custom" onClick={onRegisterNotification}>
              Đăng kí
            </button>
            <span className={`${validEmail ? "d-none" : "invalid-feedback d-block"} `}>Email không đúng định dạng</span>
          </div>
        </div>
      </div>
      <div className="main-footer widgets-dark typo-light">
        <div className="box-footer">
          <div className="row">
            <div className="col-xs-12 col-sm-6 col-md-3">
              <div className="widget subscribe no-box">
                <h5 className="widget-title">
                  KADON COLLECTIVE
                  <span />
                </h5>
                <p>Chúng tôi luôn chào đón Khách hàng tại vườn Kadon</p>
                <img src="assets/image/logo.png" width="150" alt="logo"></img>
              </div>
            </div>
            <div className="col-xs-12 col-sm-6 col-md-3">
              <div className="widget no-box">
                <h5 className="widget-title">
                  Menu
                  <span />
                </h5>
                <ul className="thumbnail-widget">
                  <li>
                    <div className="thumb-content">
                      <Link to="/">Trang chủ</Link>
                    </div>
                  </li>
                  <li>
                    <div className="thumb-content">
                      <Link to="/introduce">Giới thiệu</Link>
                    </div>
                  </li>
                  <li>
                    <div className="thumb-content">
                      <Link to="/category">Cửa hàng</Link>
                    </div>
                  </li>
                  <li>
                    <div className="thumb-content">
                      <Link to="/blog">Bếp Kadon</Link>
                    </div>
                  </li>
                  <li>
                    <div className="thumb-content">
                      <Link to="/contract">Liên hệ</Link>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-xs-12 col-sm-6 col-md-3">
              <div className="widget no-box">
                <h5 className="widget-title">
                  Bắt đầu
                  <span />
                </h5>
                <p>Những sản phẩm chất lượng và an toàn nhất</p>
              </div>
            </div>
            <div className="col-xs-12 col-sm-6 col-md-3">
              <div className="widget no-box">
                <h5 className="widget-title">
                  Liên hệ chúng tôi
                  <span />
                </h5>
                <p>
                  <a href="mailto:info@domain.com" title="glorythemes">
                    huynhtaro@gmail.com
                  </a>
                </p>
                <ul className="social-footer2">
                  <li>
                    <Link title="youtube" to="https://www.youtube.com/">
                      <img
                        alt="youtube"
                        width={30}
                        height={30}
                        src="data:image/png;base64,"
                      />
                    </Link>
                  </li>
                  <li>
                    <Link to="https://www.facebook.com/kadonfarm" title="Facebook">
                      <img
                        alt="Facebook"
                        width={30}
                        height={30}
                        src="data:image/png;base64,"
                      />
                    </Link>
                  </li>
                  <li>
                    <Link to="https://twitter.com" title="Twitter">
                      <img
                        alt="Twitter"
                        width={30}
                        height={30}
                        src="data:image/png;base64,"
                      />
                    </Link>
                  </li>
                  <li>
                    <Link title="instagram" to="https://www.instagram.com/">
                      <img
                        alt="instagram"
                        width={30}
                        height={30}
                        src="data:image/png;base64,"
                      />
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="footer-copyright">
        <div className="container">
          <div className="row">
            <div className="col-md-12 text-center">
              <p>Copyright Company Name © 2021. Thuộc bản quyền của Kadon</p>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};
export default Footer;
